@font-face {
  font-family: 'icomoon';
  src:  url(/siart89.github.io/react_doList/static/media/icomoon.b8f721eb.eot);
  src:  url(/siart89.github.io/react_doList/static/media/icomoon.b8f721eb.eot#iefix) format('embedded-opentype'),
    url(/siart89.github.io/react_doList/static/media/icomoon.7f813094.ttf) format('truetype'),
    url(/siart89.github.io/react_doList/static/media/icomoon.85087098.woff) format('woff'),
    url(/siart89.github.io/react_doList/static/media/icomoon.6e8115ab.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-check:before {
  content: "\f00c";
}
.icon-close:before {
  content: "\f00d";
}
.icon-remove:before {
  content: "\f00d";
}
.icon-times:before {
  content: "\f00d";
}

body {
  background-color: #ebf1fa;
}
.contanier {
  width: 40vw;
  margin: 50px auto;
  background-color: #fff;
  border: 2px double #dbdbdb;
  border-radius: 15px;
}
.button {
  line-height: 18px;
  border: none;
  border-bottom: 1px solid black;
  cursor: pointer;
}
.toDo-list {
  max-width: 500px;
  width: 100%;
  margin: 10px auto;
}
.toDo-list__title {
  font-size: 48px;
  font-family: 'Amatic SC', cursive;
  letter-spacing: 2px;
  word-spacing: 5px;
  color: #262525;
  font-weight: 700;
  text-align: center;
}
.toDo-list .add-task {
  display: flex;
  min-height: 35px;
}
.toDo-list .add-task .add-btn-cont {
  order: 2;
  flex-basis: 40px;
}
.toDo-list .add-task__btn {
  background: none;
  outline: none;
  width: 100%;
  font-size: 18px;
  height: 100%;
  background-color: #fff;
}
.toDo-list .add-task .input-task {
  flex: 1 1 300px;
  height: 100%;
  border: none;
  border-bottom: 1px solid black;
  font-size: 22px;
  line-height: 25px;
  outline: none;
  font-family: 'Pacifico', cursive;
  font-weight: 400;
}
.toDo-list .add-task .input-task::-webkit-input-placeholder {
  color: #e0e0e0;
}
.toDo-list .add-task .input-task::-moz-placeholder {
  color: #e0e0e0;
}
.toDo-list .add-task .input-task:-ms-input-placeholder {
  color: #e0e0e0;
}
.toDo-list .add-task .input-task::-ms-input-placeholder {
  color: #e0e0e0;
}
.toDo-list .add-task .input-task::placeholder {
  color: #e0e0e0;
}
.tasks {
  padding-top: 15px;
}
.tasks .list .task-title {
  min-height: 25px;
  border-bottom: 1px solid black;
  display: flex;
  align-items: center;
  font-size: 18px;
}
.tasks .list .task-title .check-btn__inner {
  width: 14px;
  height: 14px;
  border: 1px solid #dbdbdb;
  position: relative;
  margin-right: 5px;
  margin-left: 3px;
  cursor: pointer;
}
.tasks .list .task-title .delete-btn__inner {
  width: 14px;
  height: 14px;
  position: relative;
  margin-left: 5px;
  margin-right: 3px;
  cursor: pointer;
}
.tasks .list .task-title .delete-btn__inner_onFocus::after {
  content: "\f00d";
  position: absolute;
  font-family: 'icomoon' !important;
  top: 50%;
  left: 50%;
  color: #7d0b0b;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.tasks .list .task-title .check-btn__inner_checked::after {
  content: "\f00c";
  color: #00c760;
  position: absolute;
  font-family: 'icomoon' !important;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.tasks .list .task-title__text {
  flex: 1 1 300px;
  font-size: 20px;
  font-family: 'Pacifico', cursive;
  color: #000;
}
.tasks .list .task-title__done {
  text-decoration: line-through;
}
.count {
  display: flex;
  justify-content: space-between;
  padding: 25px 10px 10px 10px;
}
.count-unit {
  cursor: pointer;
  font-family: 'Pacifico', cursive;
  letter-spacing: 1px;
  font-size: 16px;
  color: #8a0303;
}
.count-btn_onFocus {
  font-weight: bold;
}

html, body, div, span, applet, object, iframe,
p, blockquote, pre, h1, h2, h3, h4, h5, h6,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

